<template>
	<div class="enterpriseInfo">
		<div class="enterpriseInfo_title">企业信息</div>
		<!-- 按钮 -->
		<div class="enterpriseInfo_item">
			<div class="enterpriseInfo_item_title">企业logo</div>
			<div>
				  <Avatar :src="companyInfoData.logo" size="85" />
				<!-- <img :src="companyInfoData.logo" width="200px" height="200px" /> -->
			</div>
		</div>
		<div class="enterpriseInfo_item">
			<div class="enterpriseInfo_item_title">企业名称</div>
			<div>{{companyInfoData.companyName}}
				<!-- <span style="margin-left: 15px;color: blue;">修改</span> -->
			</div>
		</div>
		<div class="border-bottom_D" style="margin-top: 20px;"></div>
		<div class="enterpriseInfo_item">
			<div class="enterpriseInfo_item_title">软件版本</div>
			<div class="flex-align-center">
				<div>{{versionList[companyInfoData.version]}}
				<!-- <span style="margin-left: 15px;color: blue;">修改</span> -->
				</div>
				<div class="mgleft_15">
					<Button type="warning" label="small">立即开通/续费</Button>
				</div>
			</div>
		</div>
		<div class="enterpriseInfo_item">
			<div class="enterpriseInfo_item_title">开通时间</div>
			<div>{{companyInfoData.creationTime}}</div>
		</div>
		<div class="enterpriseInfo_item">
			<div class="enterpriseInfo_item_title">到期时间</div>
			<div> {{companyInfoData.dueDate}}
				<span style="margin-left: 15px;color: red;">(即将到期)</span></div>
		</div>
		<div class="border-bottom_D" style="margin-top: 20px;"></div>
		<div class="enterpriseInfo_item">
			<div class="enterpriseInfo_item_title">公众号授权数</div>
			<div>{{companyInfoData.wxMpNumber}}</div>
		</div>
		<div class="enterpriseInfo_item">
			<div class="enterpriseInfo_item_title">抖音授权数</div>
			<div>{{companyInfoData.dyNumber}}</div>
		</div>
	</div>
</template>

<script>
	import {
		indexApiModules
	} from '@/api';
	export default {
		data() {
			return {
				wxAppId: localStorage.getItem('wxAppId'),
				companyInfoData:{},
				versionList:{
					1:'基础版',
					2:'专业版',
					3:'旗舰版'
				},
			}
		},
		mounted() {
			this.handgetCompanyInfo()
		},
		methods: {
			handgetCompanyInfo() {
				indexApiModules.getCompanyInfo({
					wxAppId:this.wxAppId
				}).then(res => {
					this.companyInfoData = res.data
					console.log('this.companyInfoData', this.companyInfoData)
				})
			},
		},
		created: function() {

		}
	};
</script>

<style lang="less" scoped>
	.enterpriseInfo {
		font-size: 16px;
		font-weight: 400;
		color: #333333;

		.enterpriseInfo_title {
			padding: 14px 16px;
			border-bottom: 1px solid #DDDDDD;
		}

		.enterpriseInfo_item {
			display: flex;
			align-items: center;
			margin-top: 20px;
			margin-left: 16px;

			.enterpriseInfo_item_lift {
				margin-right: 150px;
			}

			.enterpriseInfo_item_title {
				width: 130px;
			}

			.enterpriseInfo_item_input {
				margin-right: 20px;
			}

		}
	}
</style>
